import React, { useState } from 'react';
import { useProspection } from '../context/ProspectionContext';
import { PlusCircle, Send, Trash2, MessageSquare, Scan, Globe, Edit2, Linkedin, Search } from 'lucide-react';
import { ProspectForm } from '../components/ProspectForm';
import { ProspectScraper } from '../components/ProspectScraper';
import { WebProspector } from '../components/WebProspector';
import { SmartFilter } from '../components/SmartFilter';
import { MessagePreview } from '../components/MessagePreview';
import { CsvImportExport } from '../components/CsvImportExport';
import { ProspectEditor } from '../components/ProspectEditor';
import { LinkedInSearch } from '../components/LinkedInSearch';
import { Prospect } from '../types/prospect';
import { sendEmail } from '../utils/emailSender';

function Prospects() {
  const { prospects, addProspect, updateProspect, deleteProspect, generateMessage, smtpConfig } = useProspection();
  const [isAdding, setIsAdding] = useState(false);
  const [isScanning, setIsScanning] = useState(false);
  const [isWebProspecting, setIsWebProspecting] = useState(false);
  const [isLinkedInSearching, setIsLinkedInSearching] = useState(false);
  const [generatingMessage, setGeneratingMessage] = useState<string | null>(null);
  const [previewMessage, setPreviewMessage] = useState<{ prospect: Prospect; message: string } | null>(null);
  const [editingProspect, setEditingProspect] = useState<Prospect | null>(null);
  const [filteredProspects, setFilteredProspects] = useState<Prospect[]>(prospects);

  const handleGenerateMessage = async (prospect: Prospect) => {
    try {
      setGeneratingMessage(prospect.id);
      const message = await generateMessage(prospect);
      setPreviewMessage({ prospect, message });
    } catch (error) {
      alert("Erreur lors de la génération du message. Vérifiez votre clé API dans les réglages.");
    } finally {
      setGeneratingMessage(null);
    }
  };

  const handleSendMessage = async (updatedMessage?: string) => {
    if (!previewMessage) return;

    const { prospect } = previewMessage;
    const finalMessage = updatedMessage || previewMessage.message;
    
    if (!prospect.email) {
      throw new Error('Aucune adresse email définie pour ce prospect');
    }

    await sendEmail(
      prospect.email,
      `À propos de ${prospect.company}`,
      finalMessage,
      smtpConfig
    );

    updateProspect(prospect.id, {
      status: 'contacted',
      lastContact: new Date().toISOString().split('T')[0],
      interactions: [
        ...prospect.interactions,
        {
          id: Date.now().toString(),
          date: new Date().toISOString(),
          type: 'email',
          content: finalMessage
        }
      ]
    });
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">Prospects</h1>
        <div className="flex space-x-3">
          <button
            onClick={() => setIsWebProspecting(true)}
            className="flex items-center space-x-2 px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700"
          >
            <Globe className="h-5 w-5" />
            <span>Recherche web</span>
          </button>
          <button
            onClick={() => setIsLinkedInSearching(true)}
            className="flex items-center space-x-2 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
          >
            <Search className="h-5 w-5" />
            <span>Rechercher sur LinkedIn</span>
          </button>
          <button
            onClick={() => setIsScanning(true)}
            className="flex items-center space-x-2 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
          >
            <Linkedin className="h-5 w-5" />
            <span>Scanner LinkedIn</span>
          </button>
          <button
            onClick={() => setIsAdding(true)}
            className="flex items-center space-x-2 px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700"
          >
            <PlusCircle className="h-5 w-5" />
            <span>Ajouter manuellement</span>
          </button>
        </div>
      </div>

      <div className="bg-white rounded-lg shadow p-4">
        <CsvImportExport />
      </div>

      <SmartFilter 
        prospects={prospects}
        onFilteredResults={setFilteredProspects}
      />

      {isAdding && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center">
          <ProspectForm
            onSubmit={(prospect) => {
              addProspect(prospect);
              setIsAdding(false);
            }}
            onCancel={() => setIsAdding(false)}
          />
        </div>
      )}

      {isScanning && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center">
          <ProspectScraper onClose={() => setIsScanning(false)} />
        </div>
      )}

      {isWebProspecting && (
        <WebProspector onClose={() => setIsWebProspecting(false)} />
      )}

      {isLinkedInSearching && (
        <LinkedInSearch onClose={() => setIsLinkedInSearching(false)} />
      )}

      {previewMessage && (
        <MessagePreview
          prospect={previewMessage.prospect}
          message={previewMessage.message}
          onClose={() => setPreviewMessage(null)}
          onSend={handleSendMessage}
          smtpConfig={smtpConfig}
        />
      )}

      {editingProspect && (
        <ProspectEditor
          prospect={editingProspect}
          onSave={(updatedProspect) => {
            updateProspect(updatedProspect.id, updatedProspect);
            setEditingProspect(null);
          }}
          onClose={() => setEditingProspect(null)}
        />
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredProspects.map((prospect) => (
          <div key={prospect.id} className="bg-white rounded-lg shadow p-6">
            <div className="flex justify-between items-start mb-4">
              <div>
                <h3 className="text-lg font-medium text-gray-900">{prospect.name}</h3>
                <p className="text-sm text-gray-500">{prospect.position} chez {prospect.company}</p>
              </div>
              <div className="flex items-center space-x-2">
                <button
                  onClick={() => setEditingProspect(prospect)}
                  className="inline-flex items-center px-2 py-1 bg-indigo-50 text-indigo-700 rounded hover:bg-indigo-100 transition-colors"
                >
                  <Edit2 className="h-4 w-4 mr-1" />
                  <span className="text-sm">Éditer</span>
                </button>
                <span className={`px-2 py-1 text-xs font-medium rounded-full ${
                  prospect.status === 'converted' ? 'bg-green-100 text-green-800' :
                  prospect.status === 'contacted' ? 'bg-blue-100 text-blue-800' :
                  'bg-yellow-100 text-yellow-800'
                }`}>
                  {prospect.status}
                </span>
              </div>
            </div>

            <p className="text-sm text-gray-600 mb-4">{prospect.notes}</p>

            <div className="flex justify-between items-center">
              <div className="flex space-x-2">
                <button
                  onClick={() => handleGenerateMessage(prospect)}
                  disabled={generatingMessage === prospect.id}
                  className="p-2 text-gray-400 hover:text-indigo-600 transition-colors"
                >
                  {generatingMessage === prospect.id ? (
                    <MessageSquare className="h-5 w-5 animate-spin" />
                  ) : (
                    <Send className="h-5 w-5" />
                  )}
                </button>
                {prospect.linkedinUrl && (
                  <a
                    href={prospect.linkedinUrl}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="p-2 text-gray-400 hover:text-blue-600 transition-colors"
                  >
                    <Linkedin className="h-5 w-5" />
                  </a>
                )}
                <button
                  onClick={() => deleteProspect(prospect.id)}
                  className="p-2 text-gray-400 hover:text-red-600"
                >
                  <Trash2 className="h-5 w-5" />
                </button>
              </div>
              <span className="text-sm text-gray-500">
                Dernier contact: {prospect.lastContact}
              </span>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Prospects;