import { Prospect } from '../types/prospect';

export async function searchLinkedInProspects(
  company: string,
  title: string,
  apiKey: string
): Promise<Partial<Prospect>[]> {
  if (!company?.trim()) {
    throw new Error('Le nom de l\'entreprise est requis');
  }

  try {
    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${apiKey}`,
      },
      body: JSON.stringify({
        model: 'gpt-3.5-turbo',
        messages: [{
          role: 'system',
          content: `Vous êtes un expert en génération de profils LinkedIn réalistes pour des prospects français.
            Générez des profils avec des noms, emails et URLs LinkedIn réalistes.`
        }, {
          role: 'user',
          content: `Générez 5 profils LinkedIn réalistes de professionnels travaillant chez ${company}${
            title ? ` au poste de ${title} ou similaire` : ''
          }.
          
          Format JSON attendu:
          [{
            "name": "Prénom Nom",
            "email": "email professionnel",
            "company": "${company}",
            "position": "poste actuel",
            "linkedinUrl": "url linkedin",
            "notes": "description du profil",
            "tags": ["tag1", "tag2", "tag3"],
            "score": nombre entre 0-100
          }]
          
          Règles:
          - Noms et prénoms français réalistes
          - Emails professionnels cohérents avec le nom et l'entreprise
          - URLs LinkedIn au format linkedin.com/in/prenom-nom-123456
          - Notes pertinentes sur l'expérience et les compétences
          - Tags pertinents pour le secteur et le poste
          - Score basé sur la pertinence du profil`
        }],
        temperature: 0.8
      })
    });

    if (!response.ok) {
      throw new Error('Erreur lors de la recherche de prospects');
    }

    const data = await response.json();
    const prospects = JSON.parse(data.choices[0].message.content);

    return prospects.map((prospect: any) => ({
      ...prospect,
      status: 'pending',
      lastContact: new Date().toISOString().split('T')[0],
      interactions: [],
      language: 'fr'
    }));
  } catch (error) {
    console.error('LinkedIn search error:', error);
    throw new Error('Erreur lors de la recherche de prospects');
  }
}