import React, { useState } from 'react';
import { useProspection } from '../context/ProspectionContext';
import { Search, Loader, AlertCircle, Building, Users } from 'lucide-react';
import { ProspectSuggestions } from './ProspectSuggestions';
import { Prospect } from '../types/prospect';
import { searchLinkedInProspects } from '../utils/linkedinSearch';

interface LinkedInSearchProps {
  onClose: () => void;
}

export function LinkedInSearch({ onClose }: LinkedInSearchProps) {
  const { apiKey, addProspect } = useProspection();
  const [company, setCompany] = useState('');
  const [title, setTitle] = useState('');
  const [isSearching, setIsSearching] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [prospects, setProspects] = useState<Partial<Prospect>[]>([]);

  const handleSearch = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!company.trim()) {
      setError('Veuillez entrer au moins une entreprise');
      return;
    }

    setIsSearching(true);
    setError(null);
    setSuccess(null);
    setProspects([]);

    try {
      const results = await searchLinkedInProspects(company.trim(), title.trim(), apiKey);
      setProspects(results);
    } catch (err) {
      console.error('Search error:', err);
      setError(err instanceof Error ? err.message : 'Erreur lors de la recherche de prospects');
    } finally {
      setIsSearching(false);
    }
  };

  const handleAddProspect = (prospect: Partial<Prospect>) => {
    try {
      addProspect({
        ...prospect,
        status: 'pending',
        lastContact: new Date().toISOString().split('T')[0],
        interactions: [],
        tags: prospect.tags || [],
        score: prospect.score || 50,
        language: 'fr'
      });
      setSuccess(`${prospect.name} ajouté avec succès`);
      
      // Retirer le prospect ajouté de la liste
      setProspects(prospects.filter(p => p.name !== prospect.name));
    } catch (err) {
      setError('Erreur lors de l\'ajout du prospect');
    }
  };

  return (
    <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full">
      <div className="relative top-20 mx-auto p-5 border w-full max-w-4xl shadow-lg rounded-md bg-white">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold text-gray-900">Recherche LinkedIn</h2>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-500"
          >
            <span className="sr-only">Fermer</span>
            ×
          </button>
        </div>

        <form onSubmit={handleSearch} className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700">
                Entreprise
              </label>
              <div className="mt-1 relative rounded-md shadow-sm">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Building className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  type="text"
                  value={company}
                  onChange={(e) => setCompany(e.target.value)}
                  className="block w-full pl-10 pr-12 py-2 sm:text-sm border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500"
                  placeholder="ex: Apple, Google..."
                  required
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700">
                Poste recherché (optionnel)
              </label>
              <div className="mt-1 relative rounded-md shadow-sm">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Users className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  type="text"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  className="block w-full pl-10 pr-12 py-2 sm:text-sm border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500"
                  placeholder="ex: CTO, Marketing Manager..."
                />
              </div>
            </div>
          </div>

          {error && (
            <div className="rounded-md bg-red-50 p-4">
              <div className="flex">
                <AlertCircle className="h-5 w-5 text-red-400" />
                <div className="ml-3">
                  <p className="text-sm text-red-700">{error}</p>
                </div>
              </div>
            </div>
          )}

          {success && (
            <div className="rounded-md bg-green-50 p-4">
              <p className="text-sm text-green-700">{success}</p>
            </div>
          )}

          <div className="flex justify-end space-x-3">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
            >
              Annuler
            </button>
            <button
              type="submit"
              disabled={isSearching}
              className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              {isSearching ? (
                <>
                  <Loader className="animate-spin -ml-1 mr-2 h-5 w-5" />
                  Recherche en cours...
                </>
              ) : (
                <>
                  <Search className="h-5 w-5 mr-2" />
                  Rechercher
                </>
              )}
            </button>
          </div>
        </form>

        {prospects.length > 0 && (
          <div className="mt-6">
            <ProspectSuggestions
              suggestions={prospects}
              onAddSuggestion={handleAddProspect}
            />
          </div>
        )}
      </div>
    </div>
  );
}